import Vue from 'vue'
import VueResource from 'revue-resource'
Vue.use(VueResource)
import VueRouter from 'vue-router'
Vue.use(VueRouter)

const router = new VueRouter({
  mode: 'history',
  routes: [{ path: '/', component: require('./components/routes/Home.vue'), name: 'home' },
  { path: '/posts', component: require('./components/routes/Posts.vue'), name: 'posts' },
  { path: '/article/:id', component: require('./components/routes/Home.vue') },
  { path: '*', redirect: '/' }]
})

Vue.http.options.root = 'http://jsonplaceholder.typicode.com'
Vue.http.headers.common['Authorization'] = 'Basic ahskdjshjdkhsakjhd'

/* eslint-disable no-new */
new Vue({
  el: '#app',
  router,
  render: h => h(require('./App.vue'))
})



