<template>
	<div class="post">
    <h1>zhoRBen</h1>

    <div class="loader" v-if="loading">
      <div class="text">loading...</div>
    </div>

    <div class="post-wrap" v-for="post in posts">
      <input type="text" v-model="post.title">
      <div class="title">{{post.title}}</div>
      <div class="descr">{{post.body}}</div>
      <button @click="save(post)">save</button>
      <button @click="destroy(post)">close</button>
      <pre>{{post.id}}</pre>
    </div>
	</div>
</template>

<style lang="sass">
.post-wrap
  display: block
  margin-bottom: 15px
  max-width: 500px
  border: 3px solid #eee
  input
    padding: 5px 15px
    margin-top: 10px
    font-weight: bold
    font-size: 18px
    width: calc(100% - 60px)
  .title
    padding: 5px 15px
    font-weight: bold
    text-transform: uppercase
    text-align: center
  .descr
    padding: 15px
    text-align: left
  button
    border-radius: 0
    width: 140px
    height: 30px
    text-transform: uppercase
    text-align: center
    margin-bottom: 15px
    margin-right: 10px
.loader
  position: fixed
  left: 0
  top: 0
  background-color: rgba(0,0,0,.7)
  width: 100%
  height: 100%
  .text
    position: absolute
    top: 50%
    left: 50%
    margin-left: -50px
    color: #fff
</style>

<script>
export default {
  name: 'posts',

  data () {
    return {
      posts: [],
      loading: false
    }
  },

  mounted () {
    this.$post = this.$resource('posts/{/id}')
    this.$post.query().then((response) => {
      this.posts = response.data
    }, (response) => {
      console.log('error', response)
    })
  },

  methods: {
    save (post) {
      this.loading = true
      this.$post.update({id: post.id}, {title: post.title}).then((response) => {
      }, (response) => {
        console.log('error', response)
      }).then(_ => {
        this.loading = false
      })
    },

    destroy (post) {
      this.loading = true
      this.$post.remove({id: post.id}).then((response) => {
        this.posts = this.posts.filter(u => u !== post)
      }, (response) => {
        console.log('error', response)
      }).then(_ => {
        this.loading = false
      })
    }
  }
}
</script>


